<template>
  <div class="myCollect_container">
    <!-- 头部部分 -->
    <div class="myCollect_header_container">
      <div class="myCollect_top_left">
        <div>&lt;</div>
        <div>我的收藏</div>
      </div>
    </div>
    <!-- 我的收藏列表部分 -->
    <div class="collect_list_container">
      <div class="collect_list_item" v-for="(item,index) in collectList" :key="index">
        <div class="list_left">
          <div class="item_top">
            <div>{{item.title}}</div>
            <div style="color:#333333;font-size:12px;">共{{item.section_num}}课时</div>
          </div>
          <div class="item_avatar">
            <img :src="item.teachersAvatar" alt />
            <span>{{item.teachers}}</span>
          </div>
          <div class="item_bottom">
            <div style="color:#595959;fnot-size:13px;">{{item.sales_num}}人已报名</div>
            <div :style="item.price==0?'color:green':'color:orange'">{{item.price|price}}</div>
          </div>
        </div>
        <div class="list_right">
          <van-icon @click="cancelCollectClick" color="orange" name="star" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      collectList: [],
      collectID: Number
    };
  },
  filters: {
    price(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `${(val / 100).toFixed(2)}`;
      }
    }
  },
  computed: {},
  methods: {
    cancelCollectClick() {
      this.$http
        .put(`/api/app/collect/cancel/${this.collectID}/1`)
        .then(resp => {
          console.log(resp.data);
          this.$http
            .get("/api/app/collect?page=1&limit=10&type=1&")
            .then(resp => {
              console.log(resp.data);
              resp.data.data.list.forEach(ele => {
                console.log(ele.collect_id);
                this.collectID = ele.collect_id;
              });
              this.collectList = resp.data.data.list;
            });
        });
    }
  },
  mounted() {
    this.$http.get("/api/app/collect?page=1&limit=10&type=1&").then(resp => {
      console.log(resp.data);
      resp.data.data.list.forEach(ele => {
        // console.log(ele.collect_id);
        this.collectID = ele.collect_id;
      });
      this.collectList = resp.data.data.list;
    });
  }
};
</script>
<style  scoped>
.myCollect_container {
  width: 100%;
}
.myCollect_header_container {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: flex-start;
}
.myCollect_top_left {
  width: 60%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.collect_list_container {
  width: 100%;
  background: #dbd7d7;
}
.collect_list_item {
  width: 100%;
  height: 180px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #fff;
}
.item_top {
  width: 100%;
  height: 45px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: flex-start;
}
.item_avatar {
  width: 100%;
  height: 40px;
  margin-left: 10px;
}
.item_bottom {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: Center;
}
.list_left {
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.list_right {
  height: 100%;
  display: flex;
  align-content: flex-start;
  margin-top: 20px;
}
</style>